.posts .row{
    flex-wrap: wrap;
    margin: 0 -20px;
    padding: 1.25rem;
}
.posts .container{
    overflow: hidden;

}
.posts{
    max-width: 1160px;
    margin: 0 auto
}
.posts .col{
    width: 33.3%;
    padding: 2.438rem 20px;
}
.flex{
    display: flex;
}
.popup{
    position: fixed;
    width: 80%;
    max-height: 80%;
    top: 10%;
    left: 50%;
    z-index: 999;
    transform: translateX(-50%);
    max-width: 1040px;
    background: white;
    overflow-y: auto;
}
.popup-overlay{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: -998;
    background-color: aliceblue;
    opacity: 0;
    top: 0;
    left: 0;
}
.posts .close{
    position: absolute;
    right: 0;
    right: 1rem;
    top: 1rem;
}
.popup-overlay-show{opacity: .6;z-index: 996;}
.tag{
    color: #EB0028;
    font-weight: 700;
    padding: 1rem 0;
    font-size: 0.813rem;
}
.title{
    font-size:1.5rem;
    padding-bottom: 1rem;
    font-weight: 700;
}

.info{
    color: #9B9B9B;
    font-size:.75rem;
    padding-bottom: 1rem;
    font-weight:400;
}
.autor{
    color: black;
}
.date{
    padding: 0 0.813rem;
}
.text{
    font-weight: 400;
    color: #9B9B9B;
    font-size: 0.875rem;
}
.popup-content{
    margin: 0 auto;
    padding: 3rem;
    text-align: center;
}
.popup{
    max-height: 100vh;
    overflow-y: auto;
}
.popup img{
    height: 100%;
    object-fit: cover;
    object-position: top;
}
.popup .info{
    padding-bottom: 0;
    justify-content: center;
}
.popup .tag{
    padding-top: 0;
}
.popup .wrapper{
    display: flex;
    flex-wrap: wrap;
}
.popup .wrapper>picture{
    width: 40%;
}
.align-center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 60%;
    text-align: center
}
.w-100{
    width: 100%;
}

.popup picture{
    max-height: 15rem;
}
.popup .wrapper.flex>*{
    padding: .5rem;
}

@media all and (max-width: 768px){
    .posts .col{
        width: 50%;
        padding: 1rem 20px;
    }
    .popup .wrapper>picture{
        width: 100%;
    }
    .align-center{
        width: 100%;
    }
}
@media all and (max-width:480px) {
    .posts .col{
        width: 100%;
    }
}