@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body{
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-style: normal;
}
a{
    color:inherit;
    text-decoration: none;
    display: inline-block;
}
ul {
    list-style: none;
}
.row {
    display: flex;
}
.mobile{
    display:none
}
picture{
    display:inline-block;
    font-size: 0;
    width: 100%;
}
img{
    width: 100%;
}
header .row{
    position: relative;
    max-width: 1160px;
    margin: 0 auto;
    justify-content: center;
    padding: 20px
}
header .search{
    position: absolute;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
}
header {
    display: inline;
}

header nav>ul {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 1160px;
    margin: 0 auto;
}
header nav>ul>li{
    padding: 20px 1rem;
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
}
header nav>ul>li img{
    margin-left: 5px;
}
header nav{
    border-top: 1px solid #E9E9E9;
    border-bottom: 1px solid #E9E9E9;
    background-color: white;

}
nav .inner-menu li {
    cursor: pointer;
    padding: .5rem 0;
    border-bottom: 1px solid #E9E9E9;
    white-space: nowrap;
    font-weight: 400;
    font-size: 0.813rem;
    
}
nav .inner-menu a{
    display: flex;
    justify-content: space-between;
    transition: all .2s linear;
}
nav .inner-menu li:hover a{
    padding-left: .5rem;
   color: #969696;
}
nav .inner-menu li:last-child{
    padding-bottom: 0;
    border: unset;
}
nav .inner-menu li:first-child{
    padding-top: 0;
}
header nav>ul>li:hover ul{
    display: block;
    top: 59px;
}
nav .inner-menu {
    display: none;
    position:absolute;
    padding: 1.25rem;
    border: 1px solid #E9E9E9;
    border-top-color: transparent;
    min-width: 178px;
    background-color: white;

}

main {
    max-width: 1160px;
    margin: 0 auto;
}

.menu {
    position: sticky;
    top: 0;
}

.anim-hide {
    animation: hide .3s linear 1 alternate forwards;
}

.anim-show {
    animation: show .3s linear 1 alternate forwards;
}
.overlay{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
    z-index: -777;
    opacity: 0;
    transition: all .3s linear;
}
.overlay-show{
    z-index: 776;
    opacity: .6;
}
@keyframes show {
    from {
        transform: translateY(0);
    }

    to {
        transform: translateY(-100%);
    }
}

@keyframes hide {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0);
    }
}

.search input{
    position: absolute;
    right: 2.5rem;
    width: 0;
    top: 1rem;
    opacity: .95;
    top: -5px;
    border: none;
    box-shadow: 0px 0px 6px gray;
    transition: all .3s linear;
    padding: .5em 0;
    outline: none;
}
.search input:focus{
    box-shadow: 0px 0px 6px gray, 5px 5px 7px rgb(137, 219, 158);

}
.search .input-show{
    padding: .5rem;
    width: 20rem;
}

@media all and (max-width: 768px){
    .search input{
        top: 1rem;
        right: 3rem;
        
    }
    .search .input-show{
        width: 60vw;
    }
    .anim-hide,.anim-show{
        animation: unset;
    }
    .mobile{
        display:block
    }
    header,main{
        padding: 20px 0;
    }
    header .search{
        position: static;
        transform: unset;
        right: 0;
    }
    header .row{
        justify-content: space-between;
        align-items: center;
        position: fixed;
        width: 100%;
    }
    header nav > ul{
        flex-direction: column;
        align-items: start;
    }
    nav li{
        flex-wrap: wrap;
    }
    nav .inner-menu{
        position: static;
        width: 100%;
    }
    main{
        overflow: hidden;
    }
    .menu{
        transform: translateX(-100%);
        padding: 2rem 0;
        min-height: 100vh;
        width: 80%;
        position: fixed;
        top: 0;
        background-color: white;
        border-right: 1px solid #E9E9E9;
        z-index: 777;
        transition: all .3s linear;

    }
    .menu-show{
        transform: translateX(0);

    }
    .menu>.mobile{
        padding-bottom: 1.5rem;
    }
    .menu .flex,nav>ul{
        padding: 0 20px;
    }
    nav>ul>li{
        padding: 1rem 0;
        border-bottom: 1px solid #E9E9E9;
        width: 100%;
    }
    nav>ul>li:last-child{
        border-bottom: unset;
    }
    .flex{
        display: flex;
        justify-content:space-between;
    }
}